
import './App.css'
import ok from "./ok.jpg"
import usersApi from './api/users'
import { uuid } from 'uuidv4';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useState, useEffect } from 'react';

function App() {
  // Khởi tạo users
  const [users , setUsers] = useState([])
  const [username,setUsername] = useState('')
  const [email,setEmail] = useState('')
  // Set account admin
  const [admin,setAdmin] = useState([])
  const [adm,setAdm] = useState('')
  const [stt, setStt]  = useState(false)
  // 

// ______________________________________
// READ
  // Sử dụng useEffect để lấy dữ liệu api
  useEffect(() => {
   usersApi.get("/users")
      .then(res => {
        setUsers(res.data);
      })
      .catch(error => console.log(error))
  }, []);
// ______________________________________
// CREATE
  // Thêm dữ liệu vào Rest API
  function addUser(e) {
    e.preventDefault();
    if (username === "" || email === "") {
      alert("Không để trống");
      return;
    }
    // tạo ra một ofbject
    const response = {
      // id: uuid,
      name : username,
      email
    }
    // Set mảng User thêm một response
    setUsers([...users,response])
    // Gửi dữ liệu đi
    usersApi.post("/users",response)
    // Trả lại giá trị ban đầu cho
    setUsername('')
    setEmail('')

  }
// 
// ___________________________________
// DELETE
  function handleDelete(id) {
   
    usersApi.delete(`/users/${id}`)
    const newUsers = users.filter((userD) => {
      return userD.id !== id;
    });
    

    setUsers(newUsers);
  }
//  Verification
  useEffect(() => {
    usersApi.get("/admin")
      .then(res => {
        setAdmin(res.data);
      })
      .catch(error => console.log(error))
  }, []);

  function  isAdmin() {
    
    if (admin[0].keyword === adm){
      setAdm('')
      toast("Hello admin!")
      
      setStt(true)
      return
    }
    setAdm('')
    toast("Đéo phải admin! Cút")
  }
 
  console.log(stt)
  
// 
// 
  return (
    <div className="App" style={{color : "black"}}>
       <img src={ok} alt="" className='ok' />
      <h2>Bình luận trước đó</h2>
      <br></br>
     {users.map((user) => (
       <div key={user.id} className="cmt">
         <li className='cmt__name'>Name: {user.name}</li>
         <li className='cmt__content'>{user.email}</li>
         { stt && <button onClick={() => {handleDelete(user.id)}}>Xóa</button>}
         <br></br>
       </div>
     ))}
     {/* form admin */}
    <h2>Admin</h2>
    {/* <form action=""> */}
            <label className='tieude tieude_p'>keyword:</label>
            <input
              className='nd'
             
              placeholder="Nhập mật mã bí mật"
              value={adm}
              onChange={event => setAdm(event.target.value)}
            />
            <br></br>
            { !stt && <button onClick={isAdmin} className='send '> Login </button>}
            <br></br>
            <ToastContainer />
            {stt && <button onClick={() => {toast("Đăng xuất thành công");setStt(false)}} className='send '> Đăng xuất </button>}
        
    {/* </form> */}
    <h2>Bình luận</h2>
        <form onSubmit={addUser}>
          <div className="field">
            <label className='tieude tieude_p'>Name:</label>
            <input
              className='nd'
              type="text"
              name="name"
              placeholder="Name"
              value={username}
              onChange={event => setUsername(event.target.value)}
            />
          </div>
          <div className="field">
            <label className='tieude tieude_p2'>Nội dung bình luận:</label>
            <input
              className='nd'
              type="text"
              name="Noi dung"
              placeholder="Nội dung"
              value={email}
              onChange={event => setEmail(event.target.value)}
            />
          </div>
         
          <i className="fa-solid fa-paper-plane-top"></i>
 
          <button className='send'> Send </button>
        </form>
       
    </div>
  );
}

export default App;



